import axios from "axios";
import { SET_VIDEOGAMES } from "../ActionTypes";

function setVideogames(payload){
  return{
    type: SET_VIDEOGAMES,
    payload,
  } 
};
export default function getVideogamesApi (){
  const URL_VIDEOGAMES = "http://localhost:3333/videogames";
    
    return  async (dispatch) => {
      const videogames = (await axios(URL_VIDEOGAMES)).data;
      return dispatch(setVideogames(videogames));
    }
};

export function resetVideogames(){
  return(dispatch) =>{
    dispatch(setVideogames([]));
  };
}