import { Link } from "react-router-dom/cjs/react-router-dom.min";
import video from "../Media/gamer_pcs.mp4";
import "../Styles/Landing.css";

export default function Landing() {
  return(
    <div className="landingContainer">
      <div className="textContainer">
        <h1 className="GameflixConteiner">Gameflix</h1>
        <p>By Marcelo Agustín Villagrán</p>
        <h3>Welcome</h3>
        <Link to="/Videogames">
          <button type="button" className="btnLanding">Start</button>
        </Link>
      </div>
      <div className="videoContainer">
          <video id="LandingVideo" autoPlay loop muted>
            <source src={video} type="video/mp4"/>
          </video>
      </div>
    </div>
  )
}
