import {useEffect, useState} from "react";
import Videogame from "../Components/Videogame";
import { useSelector, useDispatch } from "react-redux";
import getVideogamesApi from "../Redux/Actions/Videogames";
import "../Styles/Home.css";

export default function Home(props) {
  const {history:{push}} = props;
  const dispatch = useDispatch();
  let {videogames} = useSelector((state) => {return state});
  const initState = videogames?.slice(0,15);
  const [vgs, setVgs] = useState(initState);
  const [finder, setFinder] = useState();


  useEffect(() => {
    dispatch(getVideogamesApi());
  }, [dispatch]);

  const handleOnChangePage = (numPage) => {
    let n = numPage*15;
    let m = n-15;
    let videogamesFromStore = videogames?.slice(m, n);

    setVgs(videogamesFromStore);
};

const handleNavigate = (id) =>{
  push(`/videogame/${id}`);
  setVgs([]);
  setFinder("");
};

const handleOnClick = () =>{
  push(`/`)
};

const handleonChangeFinder = (value) =>{
  const cleanStr = /[\s`~!@#$%^&*()_|+\-=?;:'",.<>{}[\]\\/]/g;
  value = value.toLowerCase();
  const videogamesFounded = videogames.filter(el => el.name.replaceAll(cleanStr,"").toLowerCase().includes(value));
  console.log("videogamesFounded: ", videogamesFounded);
  console.log("value: ", value);
  console.log("finder: ", finder);

  videogamesFounded? setVgs(videogamesFounded) : setVgs([]);
};

  return(
    <div className="homeContentConteiner">
      <div className="Header">
        <div className="logoHeaderHomeConteiner" onClick={handleOnClick}>
          {/* <img src="" alt="logoHeaderHome" /> */}GAMEFLIX
        </div>
        <div className="pageHeaderPageConteiner">
          <input type="range" min="1" max="7" defaultValue="1" onChange={(e) => handleOnChangePage(e.target.value)}/>
        </div>
        <div className="inputHeaderHomeConteiner">
          <input className="inputHeaderHome" type="search" name="finder" value={finder} placeholder="Videogames, Genres, sources"
          onChange={(e) => handleonChangeFinder(e.target.value)} />  
        </div>
      </div>
      <div className="homeContent">
          {vgs.length
          ? vgs.map( (videogame) => {
            return <Videogame 
              key={videogame.id}
              videogame={videogame}
              navigate={handleNavigate}
            />
          })
          :videogames?.slice(0,15).map( (videogame) => {
            return <Videogame 
              key={videogame.id}
              videogame={videogame}
              navigate={handleNavigate}
            />
          })
        }
      </div>
    </div>
  )
};
